  padding: 0 30px;
}

.catalog-humburger {
  width: 15px;
  height: 12px;
  position: relative;
  margin-right: 20px;
  bottom: 1px;
}

.catalog-humburger span {
  width: 100%;
  height: 2px;
  background-color: #ffffff;
  position: absolute;
  left: 0;
}

.catalog-humburger span:first-child {
  top: 0;
}

.catalog-humburger span:nth-child(2) {
  top: 50%;
  transform: translateY(-50%);
}

.catalog-humburger span:last-child {
  bottom: 0;
}

.catalog-word {
  color: #ffffff;
  text-transform: uppercase;
  line-height: 1em;
}

.navigation {
  width: 100%;
  padding-left: 40px;
}

.navigation ul {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.navigation ul li a {
  position: relative;
}

.navigation ul li a:before {
  content: '';
  display: block;
  position: absolute;
  bottom: -10px;
  left: 0;
  width: 0;
  height: 4px;
  background-color: #be1d24;
  border-radius: 5px;
  transition: width .2s;
}

.navigation ul li a:hover {
  text-shadow: 0px 0px 1px #000000;
}

.navigation ul li a:hover:before {
  width: 100%;
}

.navigation ul li .nav_active {
  text-shadow: 0px 0px 1px #000000;
}

.navigation ul li .nav_active:before {
  width: 100%;
}

.main {
  padding-top: 96px;
  margin-bottom: 112px;
}

.main-slider {
  position: relative;
  border-radius: 10px;
  max-width: 992px;
}

.main-slider__navigation {
  position: absolute;
  top: 50%;
  transform: translate(-50%);
  width: 100%;
}

.main-slider__btn {
  width: 40px;
  height: 40px;
  display: block;
  border-radius: 50%;
  position: relative;
  background-color: #f5f5f5;
}
