// main project styles
* {
  box-sizing: border-box;
}

html {
  font-size: 100%;
}

body {
  font-size: 16px;
  font-weight: 400;
  font-family: $font-primary;
  color: $black;
  background-color: $gray-body;
  -webkit-font-smoothing: antialiased;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  text-rendering: geometricPrecision;
}

.main-wrapper {
  width: 100%;
  @include d(1919) {
    max-width: 1440px;
    margin: 0 auto;
  }
}
.container {
  max-width: 1310px;
  margin: 0 auto;
}

.body_fixed {
  overflow: hidden;
}

a {
  display: inline-block;
  text-decoration: none;
  color: inherit;
  font-size: 16px;
  line-height: 1.5;

  &:visited {
    text-decoration: none;
    color: inherit;
  }

  &:active,
  &:focus {
    outline: none;
    text-decoration: none;
  }

  @include d(1499) {
    font-size: 14px;
  }
}

p {
  font-size: 16px;
  line-height: 1.5em;
  margin: 0;

  @include d(1499) {
    font-size: 14px;
  }
}

ul,
ol {
  list-style: none;
  padding-left: 0;
  margin: 0;
}

img,
picture {
  width: 100%;
  height: 100%;
  display: block;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 500;
}

h2 {
  font-size: 32px;
  line-height: 1em;
}

h3 {
  font-size: 24px;
  line-height: 1.33em;
}

section {
  position: relative;
}

// text colors
.white {
  color: $white;
}

.black {
  color: $black;
}

.bg-gray {
  background-color: #f5f5f5;
}

input {
  font-size: 16px;
  border: none;
  width: 100%;
  padding: 12px 16px;

  @include d(1499) {
    font-size: 14px;
  }

  @include d(1199) {
    font-size: 12px;
  }

  &::-webkit-input-placeholder {
    font-family: $font-primary;
    font-size: 16px;
    color: $black;

    @include d(1499) {
      font-size: 14px;
    }

    @include d(1199) {
      font-size: 12px;
    }
  }

  &::-moz-placeholder {
    font-family: $font-primary;
    font-size: 16px;
    color: $black;

    @include d(1499) {
      font-size: 14px;
    }

    @include d(1199) {
      font-size: 12px;
    }
  }

  &:-ms-input-placeholder {
    font-family: $font-primary;
    font-size: 16px;
    color: $black;

    @include d(1499) {
      font-size: 14px;
    }

    @include d(1199) {
      font-size: 12px;
    }
  }

  &::placeholder {
    font-family: $font-primary;
    font-size: 16px;
    color: $black;

    @include d(1499) {
      font-size: 14px;
    }

    @include d(1199) {
      font-size: 12px;
    }
  }
}

input[type="search"] {
  &:focus {
    outline: none;
  }

  &::-ms-clear {
    display: none;
    width: 0;
    height: 0;
  }

  &::-ms-reveal {
    display: none;
    width: 0;
    height: 0;
  }

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }

  &::-ms-clear {
    display: none;
    width: 0;
    height: 0;
  }

  &::-ms-reveal {
    display: none;
    width: 0;
    height: 0;
  }

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

textarea {
  resize: none;
}